.normal-navbar{
    position: relative;
    top: 0;
    left: 0;
    height: 90px;
    z-index: 1000;
    display: flex;
    padding: 20px 40px;
    align-items: center;
    justify-content: space-between;
  }
  .navbar__menu-normal {
    display: flex;
    align-items: center;
    gap: 45px;
  }
  .navbar__item{
  list-style-type: none;
  }
  
  .navbar__link-normal {
    text-decoration: none;
    font-size: min(6vmin, calc(1rem + 0.23vmax));
    font-family: 'Geo';
    color: #000000;
  }
  
  .navbar__link-normal:hover {
    border-bottom: 2px solid #40B44B;
  }
  .cart-nav{
  display: none;
  }
  .navbar__cta {
    display: flex;
    align-items: center;
  }
  
  .navbar__cta-btn {
    transition: all 0.42s ease;
    color: #fff;
    margin-right: 15px;
  }
  .navbar__cta-menu {
    display: none;
  }
  .navbar__cta-btn-menu{
    display: none;
    /* position: fixed;
    right: 10px; */
  }
  .log-in{
    display: none;
  }
  
  .navbar__cta-img-normal {
    filter: brightness(0) contrast(100%);
  }
  
  .navbar__cta-img-normal:hover {
    transform: scale(1.1);
    filter: brightness(0) contrast(100%);
  }